import React, { useEffect, useState } from 'react'
import loadingGif from '../../loading.gif'
import logo from '../../logo-oscuro.png'

const isValidUrl = (url) => {
    const regex = /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_+.~#?&//=]*)/
    const validUrl = regex.test(url)
    return validUrl
}

export default function LinkPrev({ url }) {

    const [loading, setLoading] = useState(true)
    const [preview, setPreviewData] = useState({})
    const [isUrlValid, setUrlValidation] = useState(false)

    const api = 'https://lpdg.herokuapp.com/parse/link'

    useEffect(() => {
        async function fetchData() {
            const fetch = window.fetch
            if (isValidUrl(url)) {
                setUrlValidation(true)
            } else {
                return {}
            }
            setLoading(true)
            const response = await fetch(api, {
                method: 'POST',
                headers: {
                    Accept: 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ url })
            })
            const data = await response.json()
            setPreviewData(data)
            setLoading(false)
        }
        fetchData()
    }, [url])

    if (!isUrlValid) {
        console.error(
            'LinkPreview Error: You need to provide url in props to render the component'
        )
        return null
    }

    return loading
        ? <img src={loadingGif} alt="loading" />
        : (
            <div class="card" style="width: 18rem;">
                <img src={preview.img || logo} class="card-img-top" alt={preview.title} />
                <div class="card-body">
                    <h5 class="card-title">{preview.title}</h5>
                    <p class="card-text">{preview.description}</p>
                    <a href={preview.domain} target='_blank' className="btn btn-primary">Visitar página</a>
                </div>
            </div>
        )

}
